"use client";

import Link from "next/link";
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import './globals.css';
import { authService, User } from '@/lib/auth-service';
import { supabase } from '@/lib/supabase';

export default function Home() {
  const [users, setUsers] = useState<User[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const router = useRouter();
  
  // –î–∞–Ω–Ω—ã–µ —Ñ–æ—Ä–º—ã
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [currentUser, setCurrentUser] = useState<User | null>(null);

  // –ü—Ä–æ–≤–µ—Ä—è–µ–º –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—é
  useEffect(() => {
    const user = authService.getCurrentUser();
    setCurrentUser(user);
    loadUsers();
  }, []);

  const loadUsers = async () => {
    setIsLoading(true);
    try {
      const { data, error } = await supabase
        .from('users')
        .select('*')
        .order('created_at', { ascending: false });
      
      if (error) {
        console.error('–û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π:', error);
        return;
      }
      
      setUsers(data || []);
    } catch (error) {
      console.error('–û—à–∏–±–∫–∞:', error);
    }
    setIsLoading(false);
  };

  const handleRegister = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!name.trim() || !email.trim()) {
      alert('–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –≤—Å–µ –ø–æ–ª—è');
      return;
    }

    try {
      // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —É–∂–µ —Ç–∞–∫–æ–π email
      const { data: existingUser } = await supabase
        .from('users')
        .select('*')
        .eq('email', email.trim())
        .single();
      
      if (existingUser) {
        // –ï—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —É–∂–µ –µ—Å—Ç—å, –ø—Ä–æ—Å—Ç–æ –∞–≤—Ç–æ—Ä–∏–∑—É–µ–º
        authService.setCurrentUser(existingUser);
        setCurrentUser(existingUser);
        alert(`–î–æ–±—Ä–æ –ø–æ–∂–∞–ª–æ–≤–∞—Ç—å, ${existingUser.name}!`);
        return;
      }
      
      // –°–æ–∑–¥–∞–µ–º –Ω–æ–≤–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
      const { data: newUser, error } = await supabase
        .from('users')
        .insert([
          { 
            name: name.trim(),
            email: email.trim()
          }
        ])
        .select()
        .single();
      
      if (error) {
        console.error('–û—à–∏–±–∫–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏:', error);
        alert('–û—à–∏–±–∫–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏');
        return;
      }
      
      // –°–æ—Ö—Ä–∞–Ω—è–µ–º –∏ –∞–≤—Ç–æ—Ä–∏–∑—É–µ–º
      authService.setCurrentUser(newUser);
      setCurrentUser(newUser);
      setName('');
      setEmail('');
      loadUsers();
      
      alert(`–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è —É—Å–ø–µ—à–Ω–∞! –î–æ–±—Ä–æ –ø–æ–∂–∞–ª–æ–≤–∞—Ç—å, ${newUser.name}`);
      
    } catch (error) {
      console.error('–û—à–∏–±–∫–∞:', error);
      alert('–û—à–∏–±–∫–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏');
    }
  };

  const handleLogout = () => {
    authService.logout();
    setCurrentUser(null);
    alert('–í—ã –≤—ã—à–ª–∏ –∏–∑ —Å–∏—Å—Ç–µ–º—ã');
  };

  const handleDeleteUser = async (id: string) => {
    if (confirm('–£–¥–∞–ª–∏—Ç—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è?')) {
      const { error } = await supabase
        .from('users')
        .delete()
        .eq('id', id);
      
      if (error) {
        console.error('–û—à–∏–±–∫–∞ —É–¥–∞–ª–µ–Ω–∏—è:', error);
        return;
      }
      
      loadUsers();
      if (currentUser?.id === id) {
        handleLogout();
      }
    }
  };

  return (
    <>
      <header className="header">
        <h1>–°–∏—Å—Ç–µ–º–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏</h1>
      </header>
      
      <main className="main">
        {/* –ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ —Ç–µ–∫—É—â–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ */}
        {currentUser ? (
          <div style={{
            maxWidth: '600px',
            margin: '0 auto 30px',
            padding: '20px',
            background: '#e8f5e8',
            borderRadius: '10px',
            textAlign: 'center'
          }}>
            <h2>üëã –î–æ–±—Ä–æ –ø–æ–∂–∞–ª–æ–≤–∞—Ç—å, {currentUser.name}!</h2>
            <p>–í—ã –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω—ã –∫–∞–∫: {currentUser.email}</p>
            
            <div style={{ display: 'flex', gap: '15px', justifyContent: 'center', marginTop: '20px' }}>
              <Link href="/blog" style={{
                padding: '10px 20px',
                background: '#08572f',
                color: 'white',
                textDecoration: 'none',
                borderRadius: '5px',
                fontWeight: 'bold'
              }}>
                üìù –ü–µ—Ä–µ–π—Ç–∏ –≤ –±–ª–æ–≥
              </Link>
              
              <button 
                onClick={handleLogout}
                style={{
                  padding: '10px 20px',
                  background: '#dc3545',
                  color: 'white',
                  border: 'none',
                  borderRadius: '5px',
                  cursor: 'pointer',
                  fontWeight: 'bold'
                }}
              >
                –í—ã–π—Ç–∏
              </button>
            </div>
          </div>
        ) : (
          /* –§–æ—Ä–º–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ */
          <div style={{
            maxWidth: '500px',
            margin: '0 auto 30px',
            padding: '30px',
            background: '#f8f9fa',
            borderRadius: '15px',
            boxShadow: '0 4px 15px rgba(0,0,0,0.1)'
          }}>
            <h2 style={{ textAlign: 'center', color: '#08572f', marginBottom: '25px' }}>
              –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è
            </h2>
            
            <form onSubmit={handleRegister} style={{ display: 'flex', flexDirection: 'column', gap: '20px' }}>
              <input
                type="text"
                placeholder="–í–∞—à–µ –∏–º—è"
                value={name}
                onChange={(e) => setName(e.target.value)}
                style={{
                  padding: '15px',
                  border: '2px solid #ddd',
                  borderRadius: '8px',
                  fontSize: '16px'
                }}
                required
              />
              
              <input
                type="email"
                placeholder="–í–∞—à email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                style={{
                  padding: '15px',
                  border: '2px solid #ddd',
                  borderRadius: '8px',
                  fontSize: '16px'
                }}
                required
              />
              
              <button type="submit" style={{
                padding: '15px',
                background: '#08572f',
                color: 'white',
                border: 'none',
                borderRadius: '8px',
                fontSize: '16px',
                fontWeight: 'bold',
                cursor: 'pointer'
              }}>
                üìù –ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è
              </button>
              
              <p style={{ textAlign: 'center', color: '#666', fontSize: '14px' }}>
                –ü–æ—Å–ª–µ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ –≤—ã —Å–º–æ–∂–µ—Ç–µ –ø–∏—Å–∞—Ç—å –ø–æ—Å—Ç—ã –≤ –±–ª–æ–≥–µ –æ—Ç —Å–≤–æ–µ–≥–æ –∏–º–µ–Ω–∏
              </p>
            </form>
          </div>
        )}

        {/* –°–ø–∏—Å–æ–∫ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π */}
        <div style={{
          maxWidth: '800px',
          margin: '0 auto',
          padding: '20px'
        }}>
          <h2 style={{ textAlign: 'center', color: '#08572f', marginBottom: '20px' }}>
            –ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–∏ ({users.length})
          </h2>
          
          {isLoading ? (
            <p style={{ textAlign: 'center', color: '#08572f' }}>–ó–∞–≥—Ä—É–∑–∫–∞...</p>
          ) : users.length === 0 ? (
            <p style={{ textAlign: 'center', color: '#08572f' }}>–ù–µ—Ç –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π</p>
          ) : (
            <div style={{
              display: 'flex',
              flexDirection: 'column',
              gap: '15px'
            }}>
              {users.map(user => (
                <div key={user.id} style={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  alignItems: 'center',
                  padding: '20px',
                  background: 'white',
                  borderRadius: '10px',
                  border: '1px solid #ddd',
                  boxShadow: '0 2px 5px rgba(0,0,0,0.1)'
                }}>
                  <div style={{ display: 'flex', flexDirection: 'column', gap: '5px' }}>
                    <strong style={{ color: '#08572f', fontSize: '18px' }}>{user.name}</strong>
                    <span style={{ color: '#0b311f', fontSize: '14px' }}>{user.email}</span>
                    <small style={{ color: '#666', fontSize: '12px' }}>
                      –ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω: {new Date(user.created_at).toLocaleDateString('ru-RU')}
                    </small>
                  </div>
                  
                  {currentUser?.id !== user.id && (
                    <button 
                      onClick={() => handleDeleteUser(user.id)}
                      style={{
                        padding: '8px 16px',
                        background: '#dc3545',
                        color: 'white',
                        border: 'none',
                        borderRadius: '6px',
                        cursor: 'pointer',
                        fontSize: '14px'
                      }}
                    >
                      –£–¥–∞–ª–∏—Ç—å
                    </button>
                  )}
                </div>
              ))}
            </div>
          )}
        </div>
      </main>
      
      <footer className="footer">
        {currentUser 
          ? `–í—ã –≤–æ—à–ª–∏ –∫–∞–∫: ${currentUser.name}` 
          : '–î–ª—è –Ω–∞–ø–∏—Å–∞–Ω–∏—è –ø–æ—Å—Ç–æ–≤ —Ç—Ä–µ–±—É–µ—Ç—Å—è —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è'}
      </footer>
    </>
  );
}